import axios from 'axios';
import { useState } from 'react';
import AddPopup from '../popup';

export default function CreatePlatform() {
  const apiKey = "https://bwithf.onrender.com";
  const [productdata, setProductData] = useState({ name: "" });
  const [errmassage, seterrmassage] = useState('')

  const addPlatform = async () => {
    try {
      const { name } = productdata;
      const name1 = name.toString();
  
      const token = localStorage.getItem('token');
      const response = await axios.post(
        `${apiKey}/api/Platform/addPlatform`,
        { name: name1 },
        { headers: { authtoken: token } }
      );
  
      if (response.data.success) {
        setProductData({ name: "" });
        seterrmassage(
          <>
            <p className="text-green-600 font-semibold text-lg mb-2">{response.data.message}</p>
            <div className="relative w-full h-2 rounded-full overflow-hidden bg-green-200 mt-4">
              <div className="absolute top-0 left-0 h-full bg-green-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
            </div>
          </>
        );
      } else {
        seterrmassage(
          <>
            <p className="text-red-600 font-semibold text-lg mb-2">{response.data.message}</p>
            <div className="relative w-full h-2 rounded-full overflow-hidden bg-red-200 mt-4">
              <div className="absolute top-0 left-0 h-full bg-red-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
            </div>
          </>
        );
      }
  
      handleAddItem();
  
    } catch (error) {
      console.error('Error:', error);
    }
  };
  

  const handleChange = (e) => {
    setProductData({ ...productdata, [e.target.name]: e.target.value });
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      addPlatform();
    }
  };

  const [showPopup, setShowPopup] = useState(false);

  const handleAddItem = () => {
      // Simulate an add item action
      setShowPopup(true);
  };

  const handleClosePopup = () => {
      setShowPopup(false);
  };

  return (
    <>
      <div className="mt-4 lg:w-full">
      <div className="App">
            
            <AddPopup show={showPopup} onClose={handleClosePopup} message={errmassage}  />
       </div>
        <div className="bg-[#f2f2f2] w-full text-black px-2 gap-6">
          <div className="bg-red-300 w-4/5 m-auto">
            <input
              type="text"
              name="name"
              onChange={handleChange}
              onKeyPress={handleKeyPress}
              value={productdata.name}
              placeholder="Platform name"
              className="px-3 py-1 border-black border-2 w-full rounded-md"
            />
          </div>
        </div>
        <div className="flex justify-center mt-5 w-full">
          <button
            onClick={addPlatform}
            className="bg-blue-500 text-white font-bold py-2 px-4 w-1/2 rounded hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
          >
            Add Platform
          </button>
        </div>
      </div>
    </>
  );
}
